footer {
    padding: 20px 0;
    background: $cinza;
    @include box-shadow(0 0 0 10px $cinza);
    p {
        color: $bege-claro;
        @include dispositivo(pequeno){
            text-align:  center;
        }
    }
    a img {
        float: right;
        @include dispositivo(pequeno) {
            float: none;
            margin: 20px auto;
        }
    }
}